h2.dekstop {
  text-align: center;
  margin-bottom: 60px;
}
.portrait-section.dekstop {
  height: 892px;
}

.container.dekstop {
  display: flex;
  justify-content: space-between;
}

ul img {
  width: 453px;
  height: 679.42px;
}

.btn-discover.dekstop {
  display: block;
  margin: 0 auto;
  width: 349px;
  height: 40px;
  background: none;
  border: 1px solid #dc1818;
  margin-top: 60px;
  cursor: pointer;
}

h2.mobile {
  text-align: center;
  margin-bottom: 0px;
  font-size: 24px;
}
.portrait-section__gallery.mobile {
  height: 700px;
  width: 100%;
  margin-top: 40px;
}

.container.mobile {
  display: flex;
  justify-content: center;
}

.btn-discover.mobile {
  display: block;
  margin: 0 auto;
  width: 349px;
  height: 40px;
  background: none;
  border: 1px solid #dc1818;

  cursor: pointer;
}

.portrait-photo.mobile {
  margin-top: 20px;
  margin-bottom: 20px;
}
