.burger-menu {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  padding: 10px;
  width: 30px;
  position: fixed;
  bottom: calc(100% - 150px); /* Raznitsa ot niza ekranu */
  right: 20px;
  z-index: 999;
}

.bar1,
.bar2,
.bar3 {
  width: 25px;
  height: 3px;
  background-color: #333;
  margin: 4px 0;
  transition: 0.4s;
}

.open .bar1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.open .bar2 {
  opacity: 0;
}

.open .bar3 {
  transform: rotate(45deg) translate(-5px, -6px);
}

.sidebar {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: -1000px;
  background-color: #fffffff0;
  transition: left 0.3s ease-in-out;
}

.sidebar.open {
  left: 0;
}

ul {
  /* padding: 50px; */
  list-style: none;
}

.nav-list-item.mobile {
  display: flex;
  flex-direction: column;
  padding: 50px;
  cursor: pointer;
  font-size: 25px;
}
